}

.shadow {
  transition: box-shadow 0.5s ease-in-out;
}

.shadow:hover {
  box-shadow: 4px 2px 9px 0px black;
}

a {
  text-decoration: none;
}

.cursor {
  width: 14em;
  margin: 0 auto;
  font-size: 20px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
body {
