}

body.night-mode {
  width: 98.9vw;
  height: 98.9vh;
  background: linear-gradient(334deg, #aa36a5, #aa36a5, #aa36a5, #aa36a5, #aa36a5, #9d48b4, #8f56c1, #7e63cc, #517bdb, #058ede, #009dd9, #00aace);
